


















import {Component} from 'vue-property-decorator';
import Button from '@/components/Button.vue';
import {mixins} from 'vue-class-component';
import appHelper from '@/mixins/appHelper';

@Component({
  components: {Button}
})
export default class Labels extends mixins(appHelper) {
  type: DataSource = {
    text: '',
    value: '-'
  };
  dataSource = [{text: '', value: '-'}, {text: '', value: '+'}];

  get tagList() {
    return (this.$store.state as RootState).tagList.filter(tag => tag.type.value === this.type.value);
  }

  remove(id: number) {
    this.$store.commit('removeTag', id);
  }

  created() {
    this.type = this.$route.query as DataSource;
    this.$store.commit('initTagList');
  }
}
