






















import Vue from 'vue'
import {Component} from 'vue-property-decorator';
import Tags from '@/components/money/Tags.vue';
import Button from '@/components/Button.vue';

type defaultList = {
  type: DataSource;
  text: string;
  name: string;
}
@Component({
  components: {Tags, Button}
})
export default class EditLabel extends Vue {
  type: DataSource = {} as DataSource;
  text = '';
  selectedTag: Tag = {id: 0, type: {text: '', value: '-'}, text: '', name: 'bus'};

  get tagList() {
    return (this.$store.state as RootState).iconList;
  }

  get newTag() {
    return {
      type: this.type,
      text: this.text,
      name: this.selectedTag.name,
    };
  }

  save() {
    if (this.text === '') {
      window.alert('');
    } else if (this.selectedTag.id === 0) {
      window.alert('');
    } else {
      this.$store.commit('createTagList', this.newTag);
      if ((this.$store.state as RootState).duplicate === 'duplicate') {
        window.alert('');
        return;
      }
      window.alert('');
      this.$router.back();
    }

  }

  mounted() {
    this.$store.commit('initIcon');
  }

  created() {
    this.type = this.$route.query as DataSource;
    this.$store.commit('initTagList');
  }
}
